export default {
	data() {
		return {
			// modal默认不显示
			modal:null,
			// 加载显示内容
			title:"审核不通过",
			// 提交内容
			content: "确定审核通过?",
			sure:"确定提交"
		}
	},
	methods: {
		show(data){
			if (typeof data === 'object' && data) {
				this.title = data.title;
				this.content = data.content;
				this.sure = data.sure;
			}
			this.modal = "show";
		},
		hide(){
			this.modal = null
		},
		submit(){
			// 触发父页面v-on:pouple方法
			this.$emit("confirm");
		}
	}
}
